import React from 'react'
import { graphql, useStaticQuery , Link} from 'gatsby'
const BlogFeatProperty=({children,title,details})=>{
    const data = useStaticQuery(graphql`
    query{
        author: file(relativePath: {eq: "property/pposter1.png"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
        }
    }`)
    return(
        
        <div className="for_blog feat_property">
            <div className="thumb">
                 <Link to="/blog/single">{children}</Link>
                <div className="blog_tag">Construction</div>
            </div>
            <div className="details">
                <Link to="/blog/single">
                <div className="tc_content">
                    <h4 className="mb15">{title}</h4>
                    <p>{details}</p>
                </div>
                </Link>
                <div className="fp_footer">
                    <ul className="fp_meta float-left mb0">
                        <li className="list-inline-item"><Link to="/agent/single"><img src={data.author.childImageSharp.fluid.originalImg} alt="pposter1.png"/> </Link></li>
                        <li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
                        <li className="list-inline-item"><a href="#"><span className="flaticon-calendar pr10"></span> January 16, 2020</a></li>
                    </ul>
                    <Link className="fp_pdate float-right text-thm" to="/blog/single">Read More <span className="flaticon-next"></span></Link>
                </div>
            </div>
        </div>
                        )
}
export default BlogFeatProperty