import React from 'react'
import {Link} from 'gatsby'

const BlogTagWidget=()=>{
    return(
    <div className="blog_tag_widget">
    <h4 className="title">Tags</h4>
    <ul className="tag_list">
        <li className="list-inline-item"><Link to="/single/v3">Apartment</Link></li>
        <li className="list-inline-item"><Link to="/single/v3">Real Estate</Link></li>
        <li className="list-inline-item"><Link to="/single/v3">Estate</Link></li>
        <li className="list-inline-item"><Link to="/single/v3">Luxury</Link></li>
        <li className="list-inline-item"><Link to="/single/v3">Real</Link></li>
    </ul>
</div>
    )
}
export default BlogTagWidget